body {
  line-height: 1.5rem;
}

.lang-button {
  position: fixed !important;
  height: 48px;
  width: 48px;
  bottom: 15px;
  right: 15px;
  color: white !important;
  font-size: 20px !important;
}

.lang-button-background {
  position: fixed !important;
  height: 48px;
  width: 48px;
  bottom: 15px;
  right: 15px;
  background: rgb(25, 118, 210);
  border-radius: 100%;
}

.frame {
  position: fixed;
  bottom: 0px;
}

#particles-js {
  position: fixed;
  top: 0px;
  bottom: 0px;
  right: 0px;
  left: 0px;
  z-index: -1;
}

.link {
  color: black;
  text-decoration: none;
  transition-duration: 0.2s;
}
.link:hover {
  color: gray;
}

.colorlink {
  color: rgb(0, 106, 255);
}

.inlineheight32 {
  display: inline-block;
  line-height: 32px;
  position: relative;
  top: -10px;
  margin-left: 10px;
}

.height32 {
  height: 32px;
  margin: 10px 0px;
}

.title {
  height: 32px;
  margin: 30px 0px;
}

ul {
  padding-inline-start: 24px;
}

.date-right {
  text-align: right;
  color: #999;
  font-family: 'Fira Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.headshot {
  float: right;
  margin-right: 24px;
}

.heightPadding {
  height: 20px;
}

.paper {
  padding: 20px 20px 10px 20px;
  margin: 50px 10px 30px 10px;
}

@media (max-width: 600px) {
  .headshot {
    float: unset;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .heightPadding {
    height: 2px;
  }

  .name {
    text-align: center;
  }

  .paper {
    margin: 0px;
  }

  .MuiContainer-root {
    padding: 0px !important;
  }
}